/*=======================*/
/*======Ribbon css ===*/
/*=======================*/
.ribbon {
  padding: 5px 15px;
  position: relative;
  margin-bottom: 15px;
  margin-top: 15px;
  z-index: 9;
}

.ribbon span {
  display: block;
  width: 100px;
  max-width: 90%;
  height: 20px;
  line-height: 20px;
  vertical-align: middle;
  color: #ffffff;
}

.ribbon.left {
  margin-left: -20px;
  text-align: left;
  width: 90%;
}

.ribbon.left:before {
  content: " ";
  height: 15px;
  width: 15px;
  border-left: 0px solid transparent;
  border-right: 15px solid transparent;
  position: absolute;
  top: 0;
  right: -15px;
}

.ribbon.left:after {
  content: " ";
  height: 15px;
  width: 15px;
  border-left: 0px solid transparent;
  border-right: 15px solid transparent;
  position: absolute;
  bottom: 0;
  right: -15px;
}

.ribbon.right {
  margin-right: -20px;
  float: right;
  text-align: right;
}

.ribbon.right:before {
  content: " ";
  height: 15px;
  width: 15px;
  border-left: 0px solid transparent;
  border-left: 15px solid transparent;
  position: absolute;
  top: 0;
  left: -15px;
}

.ribbon.right:after {
  content: " ";
  height: 15px;
  width: 15px;
  border-left: 0px solid transparent;
  border-left: 15px solid transparent;
  position: absolute;
  bottom: 0;
  left: -15px;
}

.ribbon.top-left {
  text-align: center;
  width: 40px;
  margin-top: -25px;
  padding: 0;
  margin-bottom: 30px;
}

.ribbon.top-left span {
  width: 100%;
  float: left;
  height: 40px;
  margin: 0;
  line-height: 40px;
  vertical-align: middle;
  display: block;
  max-width: none;
}

.ribbon.top-left:before {
  content: " ";
  height: 20px;
  width: 20px;
  border-bottom: 20px solid transparent;
  border-right: 20px solid transparent;
  position: absolute;
  bottom: -20px;
  left: 0;
}

.ribbon.top-left:after {
  content: " ";
  height: 20px;
  width: 20px;
  border-bottom: 20px solid transparent;
  border-left: 20px solid transparent;
  position: absolute;
  bottom: -20px;
  right: 0;
}

.ribbon.top-right {
  text-align: center;
  width: 40px;
  margin-top: -25px;
  padding: 0;
  margin-bottom: 30px;
  float: right;
}

.ribbon.top-right span {
  width: 100%;
  float: left;
  height: 40px;
  margin: 0;
  line-height: 40px;
  vertical-align: middle;
  display: block;
  max-width: none;
}

.ribbon.top-right:before {
  content: " ";
  height: 20px;
  width: 20px;
  border-bottom: 20px solid transparent;
  border-right: 20px solid transparent;
  position: absolute;
  bottom: -20px;
  left: 0;
}

.ribbon.top-right:after {
  content: " ";
  height: 20px;
  width: 20px;
  border-bottom: 20px solid transparent;
  border-left: 20px solid transparent;
  position: absolute;
  bottom: -20px;
  right: 0;
}

.ribbon.success {
  background: #2dc1c9;
  background: -moz-linear-gradient(left, #2dc1c9 0%, #2194c1 30%, #252d47 100%);
  background: -webkit-linear-gradient(
    left,
    #2dc1c9 0%,
    #2194c1 30%,
    #252d47 100%
  );
  background: linear-gradient(to right, #2dc1c9 0%, #2194c1 30%, #252d47 100%);
  color: gray;
}

.ribbon.success .progress-bar {
  background: #51d1d8;
}

.ribbon.success .background-image:before {
  background: rgba(45, 193, 201, 0.2);
  background: -moz-linear-gradient(
    left,
    rgba(45, 193, 201, 0.2) 0%,
    rgba(33, 148, 193, 0.9) 30%,
    #252d47 100%
  );
  background: -webkit-linear-gradient(
    left,
    rgba(45, 193, 201, 0.2) 0%,
    rgba(33, 148, 193, 0.9) 30%,
    #252d47 100%
  );
  background: linear-gradient(
    to right,
    rgba(45, 193, 201, 0.2) 0%,
    rgba(33, 148, 193, 0.9) 30%,
    #252d47 100%
  );
  color: gray;
}

.ribbon.success .background-image:before .progress-bar {
  background: rgba(81, 209, 216, 0.2);
}

.ribbon.success.left:before {
  border-top: 15px solid #252d47;
  border-bottom: 15px solid #252d47;
}

.ribbon.success.right:before {
  border-top: 15px solid #2dc1c9;
}

.ribbon.success.right:after {
  border-bottom: 15px solid #2dc1c9;
}

.ribbon.success.top-left {
  background: #2cc4cd;
  position: absolute;
  left: 10px;
}

.ribbon.success.top-left:before {
  border-left: 25px solid #2cc4cd;
}

.ribbon.success.top-left:after {
  border-right: 25px solid #2cc4cd;
}

.ribbon.success.top-right {
  background: #2cc4cd;
  position: absolute;
  right: 10px;
}

.ribbon.success.top-right:before {
  border-left: 25px solid #2cc4cd;
}

.ribbon.success.top-right:after {
  border-right: 25px solid #2cc4cd;
}

.ribbon.danger {
  background: #e47c64;
  background: -moz-linear-gradient(left, #e47c64 0%, #e36481 30%, #252d47 100%);
  background: -webkit-linear-gradient(
    left,
    #e47c64 0%,
    #e36481 30%,
    #252d47 100%
  );
  background: linear-gradient(to right, #e47c64 0%, #e36481 30%, #252d47 100%);
  color: gray;
}

.ribbon.danger .progress-bar {
  background: #eca18f;
}

.ribbon.danger .background-image:before {
  background: rgba(228, 124, 100, 0.2);
  background: -moz-linear-gradient(
    left,
    rgba(228, 124, 100, 0.2) 0%,
    rgba(227, 100, 129, 0.9) 30%,
    #252d47 100%
  );
  background: -webkit-linear-gradient(
    left,
    rgba(228, 124, 100, 0.2) 0%,
    rgba(227, 100, 129, 0.9) 30%,
    #252d47 100%
  );
  background: linear-gradient(
    to right,
    rgba(228, 124, 100, 0.2) 0%,
    rgba(227, 100, 129, 0.9) 30%,
    #252d47 100%
  );
  color: gray;
}

.ribbon.danger .background-image:before .progress-bar {
  background: rgba(236, 161, 143, 0.2);
}

.ribbon.danger.left:before {
  border-top: 15px solid #252d47;
  border-bottom: 15px solid #252d47;
}

.ribbon.danger.right:before {
  border-top: 15px solid #e47c64;
}

.ribbon.danger.right:after {
  border-bottom: 15px solid #e47c64;
}

.ribbon.danger.top-left {
  background: #ff6262;
  position: absolute;
  left: 10px;
}

.ribbon.danger.top-left:before {
  border-left: 25px solid #ff6262;
}

.ribbon.danger.top-left:after {
  border-right: 25px solid #ff6262;
}

.ribbon.danger.top-right {
  background: #ff6262;
  position: absolute;
  right: 10px;
}

.ribbon.danger.top-right:before {
  border-left: 25px solid #ff6262;
}

.ribbon.danger.top-right:after {
  border-right: 25px solid #ff6262;
}

.ribbon.warning {
  background: #ffba00;
  background: -moz-linear-gradient(left, #ffba00 0%, #fc9244 30%, #252d47 100%);
  background: -webkit-linear-gradient(
    left,
    #ffba00 0%,
    #fc9244 30%,
    #252d47 100%
  );
  background: linear-gradient(to right, #ffba00 0%, #fc9244 30%, #252d47 100%);
  color: gray;
}

.ribbon.warning .progress-bar {
  background: #ffc833;
}

.ribbon.warning .background-image:before {
  background: rgba(255, 186, 0, 0.2);
  background: -moz-linear-gradient(
    left,
    rgba(255, 186, 0, 0.2) 0%,
    rgba(252, 146, 68, 0.9) 30%,
    #252d47 100%
  );
  background: -webkit-linear-gradient(
    left,
    rgba(255, 186, 0, 0.2) 0%,
    rgba(252, 146, 68, 0.9) 30%,
    #252d47 100%
  );
  background: linear-gradient(
    to right,
    rgba(255, 186, 0, 0.2) 0%,
    rgba(252, 146, 68, 0.9) 30%,
    #252d47 100%
  );
  color: gray;
}

.ribbon.warning .background-image:before .progress-bar {
  background: rgba(255, 200, 51, 0.2);
}

.ribbon.warning.left:before {
  border-top: 15px solid #252d47;
  border-bottom: 15px solid #252d47;
}

.ribbon.warning.right:before {
  border-top: 15px solid #ffba00;
}

.ribbon.warning.right:after {
  border-bottom: 15px solid #ffba00;
