/*=======================*/
/* header top navbar css */
/*=======================*/
header {
  width: 100%;
  display: block;
  position: relative;
  z-index: 10;
  -webkit-transition: 0.5s ease all;
  -moz-transition: 0.5s ease all;
  -ms-transition: 0.5s ease all;
  transition: 0.5s ease all;
}

header .icon-header {
  color: #ffffff;
}

header .icon-header:hover {
  color: black;
}

header .icon-header:focus {
  color: black;
  outline: 0px none;
  -webkit-box-shadow: 0 0px 0px 0px transparent;
  -moz-box-shadow: 0 0px 0px 0px transparent;
  -ms-box-shadow: 0 0px 0px 0px transparent;
  box-shadow: 0 0px 0px 0px transparent;
}

header .navbar {
  background: #ffffff;
  -webkit-box-shadow: 0 2px 5px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 2px 5px 0px rgba(0, 0, 0, 0.1);
  -ms-box-shadow: 0 2px 5px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 5px 0px rgba(0, 0, 0, 0.1);
}

header .navbar .navbar-brand {
  font-size: 20px;
  color: #000;
}

header .nav-item .dropdown-menu {
  margin-top: 12px;
}

header .search-header,
header .nav-item,
header .nav-item .btn {
  position: relative;
}

header .search-header {
  margin: -15px 0;
}

header .search-header input[type="text"] {
  background: whitesmoke;
  color: black;
  height: 60px;
  min-width: 300px;
  border: 0;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
}

header .search-header input[type="text"]::placeholder {
  color: #999999;
}

header .search-header input[type="text"]:focus {
  border: 0px;
}

header .search-header button {
  position: absolute;
  right: 0;
  top: 10px;
}

header .sidebar-right,
header .sidebar-left {
  width: 260px;
}

header.navbar-fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.message-container {
  width: 300px;
  left: 50%;
  margin-left: -150px;
  padding: 0px;
}

.message-container:before {
  content: " ";
  height: 8px;
  width: 8px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #ffffff;
  margin-left: -9px;
  margin-top: -8px;
  left: 50%;
  position: absolute;
  z-index: 2;
}

.message-container:after {
  content: " ";
  height: 8px;
  width: 8px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #bbbbbb;
  margin-left: -9px;
  margin-top: -9px;
  left: 50%;
  position: absolute;
  top: 0;
  z-index: 1;
}

.message-container div {
  max-height: 400px;
  overflow-y: auto;
}

.message-container div .media {
  color: #666666;
  text-decoration: none;
  padding: 12px 10px;
  border-bottom: 1px solid #eeeeee;
}

.message-container div .media h6 {
  color: #000;
}

.message-container div .media:hover h6 {
  color: black;
}

.userprofile {
  padding-left: 40px !important;
  color: black;
}

.userpic {
  height: 30px;
  width: 30px;
  margin-right: 10px;
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  border-radius: 3px;
  background: #ffffff;
  position: absolute;
  left: 0;
  top: 3px;
}

.userpic img {
  height: auto;
  width: 100%;
  border: 0;
  vertical-align: top;
}

.box-links > div > .media {
  width: 33.33%;
  padding: 15px;
  float: left;
  border: 0px none !important;
}

.box-links > div > .media .quick-block {
  width: 100%;
  height: 70px;
  line-height: 70px;
  vertical-align: middle;
  text-align: center;
  border: 1px solid #eeeeee;
  font-size: 24px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  border-radius: 4px;
}

.navbar-brand.imglogo {
  height: 42px;
  width: 166px;

  background-size: auto 40px;
  margin-bottom: -5px;
  margin-top: -4px;
}

/*=======================*/
/*===== Buttons css =======*/
/*=======================*/
.btn-primary,
.btn-secondary,
.btn-info,
.btn-danger,
.btn-success {
  -webkit-box-shadow: inset 0 1px 1px 0px rgba(255, 255, 255, 0.5);
  -moz-box-shadow: inset 0 1px 1px 0px rgba(255, 255, 255, 0.5);
  -ms-box-shadow: inset 0 1px 1px 0px rgba(255, 255, 255, 0.5);
  box-shadow: inset 0 1px 1px 0px rgba(255, 255, 255, 0.5);
  border: 0;
}

.btn-primary:focus,
.btn-primary:hover {
  color: #fff;
}

.btn-round {
  -webkit-border-radius: 50px !important;
  -moz-border-radius: 50px !important;
  -ms-border-radius: 50px !important;
  border-radius: 50px !important;
}

.btn-round.input-group-addon:first-child {
  -webkit-border-radius: 50px 0 0 50px !important;
  -moz-border-radius: 50px 0 0 50px !important;
  -ms-border-radius: 50px 0 0 50px !important;
  border-radius: 50px 0 0 50px !important;
}

.btn-round.input-group-addon:not(:first-child) {
  -webkit-border-radius: 0 50px 50px 0 !important;
  -moz-border-radius: 0 50px 50px 0 !important;
  -ms-border-radius: 0 50px 50px 0 !important;
  border-radius: 0 50px 50px 0 !important;
}

/*=======================*/
/*==== Timelines ======*/
/*=======================*/
.timeline {
  margin: 15px auto;
}

.timeline .media:before {
  content: " ";
  height: 100%;
  left: 14px;
  width: 1px;
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 0;
}

.timeline .media .message_userpic {
  margin-right: 15px;
  height: 60px;
  width: 60px;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
}

.timeline .media .message_userpic img {
  height: 100%;
  width: auto;
}

.timeline .media .message_userpic:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  z-index: 1;
}

.timeline .media .message_userpic.success:before {
