.timeline .media .indication {
  margin-right: 10px;
  height: 60px;
  width: 30px;
  position: relative;
  text-align: center;
  vertical-align: middle;
  line-height: 60px;
}

.timeline .media h6 {
  margin-top: 10px;
}

.timeline .media h6 small {
  color: black;
}

.timeline_large {
  position: relative;
  padding: 20px 0 20px;
  list-style: none;
  display: inline-block;
  width: 100%;
}

.timeline_large:before {
  content: " ";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 2px;
  margin-left: -1.5px;
  background-color: #e6e6e6;
}

.timeline_large > li {
  position: relative;
  margin-bottom: 20px;
  display: inline-block;
}

.timeline_large > li:before,
.timeline_large > li:after {
  content: " ";
  display: table;
}

.timeline_large > li:after {
  clear: both;
}

.timeline_large > li > .timeline-panel {
  float: left;
  position: relative;
  width: 46%;
  padding: 20px;
  border: 1px solid #e6e6e6;
  border-radius: 0;
  background: white;
}

.timeline_large > li > .timeline-panel:before {
  content: " ";
  display: inline-block;
  position: absolute;
  top: 26px;
  right: -15px;
  border-top: 15px solid transparent;
  border-right: 0 solid #e6e6e6;
  border-bottom: 15px solid transparent;
  border-left: 15px solid #e6e6e6;
}

.timeline_large > li > .timeline-panel:after {
  content: " ";
  display: inline-block;
  position: absolute;
  top: 27px;
  right: -14px;
  border-top: 14px solid transparent;
  border-right: 0 solid white;
  border-bottom: 14px solid transparent;
  border-left: 14px solid white;
}

.timeline_large > li > .timeline-panel .timeline-heading {
  color: #ffffff;
}

.timeline_large > li > .timeline-panel p {
  margin: 15px 0 0 0;
}

.timeline_large > li > .timeline-panel figure {
  margin: 15px 0;
}

.timeline_large > li > .timeline-panel .project-list .media {
  height: auto;
}

.timeline_large > li > .timeline-panel table {
  margin: 15px 0;
}

.timeline_large > li .timeline-badge {
  z-index: 100;
  position: absolute;
  top: 16px;
  left: 50%;
  width: 50px;
  height: 50px;
  margin-left: -25px;
  border-radius: 50% 50% 50% 50%;
  text-align: center;
  font-size: 1.4em;
  line-height: 50px;
  color: #ffffff;
  background-color: #e6e6e6;
}

.timeline_large > li.timeline-inverted {
  float: right;
}

.timeline_large > li.timeline-inverted > .timeline-panel {
  float: right;
}

.timeline_large > li.timeline-inverted > .timeline-panel:before {
  right: auto;
  left: -15px;
  border-right-width: 15px;
  border-left-width: 0;
}

.timeline_large > li.timeline-inverted > .timeline-panel:after {
  right: auto;
  left: -14px;
  border-right-width: 14px;
  border-left-width: 0;
}

.icons p {
  font-size: 15px;
  line-height: 30px;
  text-align: center;
  display: block;
}

.icons p:before {
  display: block;
  color: #ffffff;
  font-size: 24px;
}

/*=======================*/
/*=======Activity blocks=====*/
/*=======================*/
.activity-block {
  background: #ffffff;
  width: 100%;
  display: block;
  float: left;
  margin: 15px auto 15px auto;
  padding: 15px 13px;
  position: relative;
}

.activity-block h5 {
  color: black;
}

.activity-block p {
  color: #8c8c8c;
  margin-bottom: 0;
  text-transform: uppercase;
}

.activity-block .progress-heading {
  font-size: 13px;
  color: #666666;
}

.activity-block i {
  font-size: 24px;
  margin-top: 10px;
}

.activity-block .progress {
  width: 100%;
  background: transparent;
  left: 0;
  bottom: -15px;
  position: relative;
  overflow: visible;
  margin-top: 5px;
  z-index: 3;
}

.activity-block .progress .progress-bar {
  height: 4px;
}

.activity-block .progress .progress-bar .trackerball {
  height: 8px;
  width: 8px;
  float: right;
  position: relative;
  top: -2px;
  background: #ffffff;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  border-radius: 4px;
  -webkit-box-shadow: 0 0px 6px 0px white;
  -moz-box-shadow: 0 0px 6px 0px white;
  -ms-box-shadow: 0 0px 6px 0px white;
  box-shadow: 0 0px 6px 0px white;
}

.activity-block .badge {
  font-size: 14px;
  vertical-align: middle;
  padding: 2px 8px;
}

.activity-block .badge i {
  font-size: 14px;
  margin: 0;
}

.activity-block .background-image {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 0;
  opacity: 1;
  top: 0;
  left: 0;
  overflow: hidden;
}

.activity-block .background-image img {
  position: relative;
  height: 100%;
  width: auto;
  float: left;
}

.activity-block .background-image:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  z-index: 1;
}

.activity-block .bg-icon {
  font-size: 100px;
  height: 100px;
  width: 100%;
  position: absolute;
  z-index: 0;
  top: 10px;
  left: 0;
  opacity: 0.1;
  padding: 0 15px;
}

.activity-block.success h5,
.activity-block.success p,
.activity-block.success .progress-heading,
.activity-block.danger h5,
.activity-block.danger p,
.activity-block.danger .progress-heading,
.activity-block.warning h5,
.activity-block.warning p,
.activity-block.warning .progress-heading,
.activity-block.primary h5,
.activity-block.primary p,
.activity-block.primary .progress-heading {
  color: black;
}

.activity-block.success .media,
.activity-block.danger .media,
.activity-block.warning .media,
.activity-block.primary .media {
  position: relative;
  z-index: 1;
}

