/*=======================*/
/*======Cards css =======*/
/*=======================*/
.card {
  margin: 15px auto 15px auto;
  background: #ffffff;
}

.card .card-img-top {
  max-width: 100%;
}

.card .card-header {
  background: transparent;
  border-bottom: 1px solid #e6e6e6;
}

.card .card-header .nav-item {
  position: relative;
}

.card .card-header .nav-item .pull-right .dropdown-menu {
  left: auto;
  right: 0px;
  margin-top: 12px;
}

.card .card-header .card-title {
  font-size: 18px;
  margin: 0;
  line-height: 30px;
  font-weight: 300;
  color: black;
  vertical-align: middle;
}

.card .card-header .card-title small {
  color: #8c8c8c;
}

.card .card-header .btn {
  line-height: 28px;
  padding: 0 12px;
  font-size: 13px;
  margin-left: 10px;
  height: 30px;
}

.card .card-header .btn i {
  margin: 0 2px;
}

.card .card-header .btn:focus {
  color: black;
  -webkit-box-shadow: 0 0px 0px 0px transparent;
  -moz-box-shadow: 0 0px 0px 0px transparent;
  -ms-box-shadow: 0 0px 0px 0px transparent;
  box-shadow: 0 0px 0px 0px transparent;
  outline: none;
}

.card .card-header .btn-link {
  padding: 0 6px;
}

.card .card-header .list-unstyled {
  overflow: visible;
}

.card .card-header .list-unstyled .media {
  padding: 5px 0;
}

.card .card-footer {
  background: white;
  border-top: 1px solid #e6e6e6;
}

/*==============================================*/
/*========== Modal and popovers ==============*/
/*==============================================*/
.modal.dark_bg .modal-dialog .modal-content {
  background: white;
  color: black;
}

.modal.dark_bg .modal-dialog .modal-content .modal-header {
  color: #ffffff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.modal.dark_bg .modal-dialog .modal-content .modal-header button.close {
  color: #ffffff;
}

.modal.dark_bg .modal-dialog .modal-content .modal-footer {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.modal .modal-dialog .modal-content {
  color: #666666;
}

.modal .modal-dialog .modal-content .modal-header {
  color: #222222;
}

.popover {
  color: #666666;
}

.popover .popover-title {
  color: #222222;
}

/*==============================*/
/*====== jqvector map CSS ======*/
/*==============================*/
.worldmap,
#mixedchartjs {
  width: 100%;
  height: 270px !important;
  padding-left: 0px;
}

.jvectormap-container {
  background: transparent !important;
  padding: 10px;
}

.hide {
  display: none !important;
}

/*==============================================*/
/*========== Full screen and search ==============*/
/*==============================================*/
.fullscreen {
  height: 100%;
  overflow: hidden;
}

.fullscreen .full-screen-container.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  margin: 0;
  height: 100%;
  width: 100%;
}

.fullscreen .wrapper-content {
  z-index: 99;
}

.fullscreen .navbar-fixed {
  z-index: 10;
}

.fullscreen .sidebar-left,
.fullscreen .sidebar-right {
  z-index: 9;
}

.fullscreen .card-block {
  max-width: 1380px;
  margin: 0 auto;
  width: 100%;
  overflow-y: auto;
}

.fullscreen .worldmap,
.fullscreen #mixedchartjs,
.fullscreen canvas {
  height: auto !important;
}

.fullscreen .comment-list,
.fullscreen .media-list,
.fullscreen .project-list,
.fullscreen .user-list {
  max-height: none;
  overflow-y: visible;
  height: auto;
}

.fullscreen .project-list .col-8 {
  width: 25%;
  flex: 0 0 25%;
  margin: 10px 0;
}

.fullscreen .hide {
  display: block !important;
}

.searchshow {
  height: 100%;
  overflow: hidden;
}

.search-block {
  position: fixed;
  height: 100%;
  width: 100%;
  overflow: auto;
  top: 0;
  left: 0;
  background: rgba(245, 245, 245, 0.95);
  z-index: 999;
  display: none;
}

.search-block .close-btn {
  position: absolute;
  height: 80px;
  width: 80px;
  line-height: 60px;
  text-align: center;
  right: 0px;
  top: 0px;
  font-size: 30px;
  padding: 0;
  z-index: 9;
}

.search-block .search-block-form {
  padding: 15px;
  width: 100%;
  position: relative;
  margin-top: 40px;
}

.search-block .search-block-form input[type="text"] {
  font-weight: 200;
  width: 100%;
  font-size: 34px;
  line-height: 60px;
  padding: 10px 20px;
  border: 0;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  -ms-border-radius: 0;
  border-radius: 0;
  border-bottom: 1px solid #e6e6e6;
  background: transparent;
  color: black;
}

.search-block .search-block-form .btn {
  position: absolute;
  height: 80px;
  width: 80px;
  line-height: 80px;
  text-align: center;
  right: 15px;
  top: 15px;
  font-size: 30px;
  padding: 0;
}

.page-link {
  background: white;
  color: black;
  border-color: #e6e6e6;
}

.page-item.disabled .page-link {
  background: white;
  color: #8c8c8c;
  border-color: #e6e6e6;
}

.progress-circle strong {
  position: absolute;
  top: 50px;
  margin-top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  line-height: 40px;
  font-size: 30px;
  color: #666666;
  font-weight: 400;
}

.progress-circle strong i {
  font-size: 50%;
}

.nav-item {
  position: relative;
}

/*==============================================*/
/* ============ playerfixed bottom ============ */
/*==============================================*/
.player-bottom .wrapper-content {
  margin-bottom: 175px;
}

.player-bottom > .sidebar-right,
.player-bottom > .sidebar-left {
  padding-bottom: 140px;
}

.player-bottom .fixed-bottom .activity-block .background-image {
  opacity: 0.4;
}

.player-bottom .fixed-bottom .activity-block .background-image img {
  width: 100%;
  height: auto;
  max-height: none;
  margin-top: -30%;
}

.player-bottom .fixed-bottom .activity-block .progress {
  top: -15px;
  bottom: auto;
  margin: 0;
  background: rgba(0, 0, 0, 0.3);
  height: 8px;
}

.player-bottom .fixed-bottom .activity-block .progress .progress-bar {
  height: 8px;
}

.player-bottom
